.header {
  justify-content: space-between;
  align-items: center;
  height: 60px;
  display: flex;
  position: relative;
  box-shadow: 0 2px 4px #0000000d;
}

.header__logo-link {
  margin-left: 50px;
  display: flex;
}

.header__logo {
  width: 40px;
  height: 40px;
}

.header__nav {
  margin-right: 50px;
}

.header__nav-list {
  margin: 0;
  padding: 0;
  line-height: 60px;
  list-style: none;
  display: flex;
}

.header__nav-item {
  text-align: center;
  margin-right: 20px;
  display: inline-block;
}

.header__nav-item:last-child {
  margin-right: 0;
}

.header__nav-link {
  color: #060b35;
  text-transform: uppercase;
  line-height: 60px;
  text-decoration: none;
  display: block;
  position: relative;
}

.header__nav-link:hover, .is-active {
  color: #00acdc;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

body {
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
}

h2 {
  margin: 0;
}

.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  justify-content: center;
  width: 200px;
  padding: 32px 16px 16px;
  transition: transform .3s;
  display: flex;
}

.card__picture {
  width: 160px;
  height: 134px;
  margin: 0 auto 40px;
  display: block;
}

.card__title {
  color: #060b35;
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  color: #616070;
  margin-bottom: 16px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__rating {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
  display: flex;
}

.card__rating-text {
  color: #060b35;
  padding-top: 1px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
  display: block;
}

.card__price {
  justify-content: space-between;
  gap: 85px;
  margin-bottom: 16px;
  display: flex;
}

.card__price-word {
  color: #616070;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
  display: inline-block;
}

.card__price-number {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  box-sizing: border-box;
  text-transform: uppercase;
  color: #fff;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 166px;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  transition: color, background-color .3s;
  display: flex;
}

.card__button:hover {
  color: #00acdc;
  background-color: #fff;
}

.card:hover {
  transform: scale(1.2);
}

.card:hover .card__title {
  color: #34568b;
}

.stars {
  justify-content: center;
  display: flex;
}

.stars__star {
  background: url("star.822d561e.svg") center no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars--1 :nth-child(-n+1), .stars--2 :nth-child(-n+2), .stars--3 :nth-child(-n+3), .stars--4 :nth-child(-n+4), .stars--5 :nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

.catalog {
  grid-template-rows: 60px auto;
  grid-template-columns: 1fr;
  column-gap: 50px;
  display: grid;
}

.content {
  grid-template-columns: repeat(4, 200px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 200px) {
  .content {
    grid-template-columns: repeat(1, 200px);
  }
}

@media (width >= 488px) {
  .content {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .content {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .content {
    grid-template-columns: repeat(4, 200px);
  }
}
